.page-info {
  position: absolute;
  z-index: 2;
  bottom: 10px;
  right: 10px;
  font-size: 18px;
  -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: white;
  text-align: right;
}

.page-info > * {
  -webkit-text-stroke-width: 0px;
  -webkit-text-stroke-color: white;
}

.thanks-to-container {
  padding: 24px;
}

.thanks-to-container > h1 {
  margin: 0;
  padding: 0;
}
